.login-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: var(--background-secondary);
  padding: 2rem;
}

.login-card {
  background: var(--background-primary);
  border-radius: var(--border-radius-large);
  box-shadow: var(--shadow-heavy);
  border: 1px solid var(--border-light);
  width: 100%;
  max-width: 400px;
  padding: 2.5rem;
}

.login-header {
  text-align: center;
  margin-bottom: 2rem;
  
  h1 {
    color: var(--text-primary);
    font-size: 2rem;
    font-weight: 600;
    margin: 0 0 0.5rem 0;
  }
  
  p {
    color: var(--text-secondary);
    font-size: 0.875rem;
    margin: 0;
  }
}

.login-form {
  .form-group {
    margin-bottom: 1.5rem;
    
    .full-width {
      width: 100%;
    }
  }
  
  .form-actions {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-top: 2rem;
    
    .login-button {
      width: 100%;
      height: 48px;
      font-size: 1rem;
      font-weight: 500;
      background: var(--primary-color);
      color: white;
      border: none;
      border-radius: var(--border-radius);
      transition: all 0.2s ease;
      
      &:hover:not(:disabled) {
        background: var(--primary-dark);
        transform: translateY(-1px);
        box-shadow: var(--shadow-medium);
      }
      
      &:disabled {
        background: var(--text-disabled);
        cursor: not-allowed;
        transform: none;
        box-shadow: none;
      }
      
      mat-icon {
        margin-right: 0.5rem;
      }
    }
    
    .help-button {
      width: 100%;
      height: 40px;
      color: var(--text-secondary);
      font-size: 0.875rem;
      border: 1px solid var(--border-color);
      border-radius: var(--border-radius);
      transition: all 0.2s ease;
      
      &:hover {
        background: var(--background-tertiary);
        color: var(--text-primary);
        border-color: var(--primary-color);
      }
      
      mat-icon {
        margin-right: 0.5rem;
        font-size: 18px;
      }
    }
  }
}

.register-section {
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid var(--border-light);
  
  .divider {
    text-align: center;
    margin-bottom: 1rem;
    
    span {
      color: var(--text-secondary);
      font-size: 0.875rem;
      background: var(--background-primary);
      padding: 0 1rem;
      position: relative;
      
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: -1rem;
        right: -1rem;
        height: 1px;
        background: var(--border-light);
        z-index: -1;
      }
    }
  }
  
  .register-button {
    width: 100%;
    height: 48px;
    font-size: 1rem;
    font-weight: 500;
    color: var(--primary-color);
    border: 2px solid var(--primary-color);
    border-radius: var(--border-radius);
    background: transparent;
    transition: all 0.2s ease;
    
    &:hover {
      background: var(--primary-light);
      border-color: var(--primary-dark);
      transform: translateY(-1px);
      box-shadow: var(--shadow-light);
    }
    
    mat-icon {
      margin-right: 0.5rem;
    }
  }
}

// Responsive design
@media (max-width: 768px) {
  .login-container {
    padding: 1rem;
  }
  
  .login-card {
    padding: 2rem;
  }
  
  .login-header h1 {
    font-size: 1.75rem;
  }
}